import React, {useState, useEffect} from 'react' 
import personsService from '../services/persons'


const PersonForm = ({setError, setMessage, setNewNum, newName, newNum, handleNameChange, handleNumChange, setPersons, persons, setNewName}) => {

  const updatePersonData = id => {
    const person = persons.find(n => n.id === id)
    const changedPerson = {... person, number: nameObject.number}

    personsService
      .update(id, changedPerson)
      .then(returnedPerson =>{
        setPersons(persons.map(person => person.id !== id ? person : returnedPerson))
      })
      .catch(error =>{
        setError(true)
        setMessage(`${nameObject.name} has already been deleted from server`)
        setTimeout(() => {
          setMessage(null)
          setError(false)
        }, 2000)
      })
      }
  

  const nameObject = {
    name: newName,
    number: newNum
  }

  const addName = (event) => {
  
        var exists = false
        event.preventDefault()
        persons.forEach((persons) => persons.name === newName ? 
        exists = true : null)
        const person = persons.find(n => n.name === newName)
       
        if(exists === false){
        personsService
          .create(nameObject)
            .then(returnedPerson => {
              setPersons(persons.concat(returnedPerson))
              setNewName('')
              setNewNum('')
              setMessage(`Added ${nameObject.name}`)
              setTimeout(() => {
                setMessage(null)
              }, 5000)
              exists = false  
            }).catch(error => {  
              // this is the way to access the error message
              console.log(error.response.data)
              setError(true)
              setMessage(`${JSON.stringify(error.response.data)}`)    
              setTimeout(() => {
                setMessage(null)
                setError(false)
              }, 2000)
            })
        
      }
      else if (window.confirm(`${newName} is already added to phonebook, replace old number with a new one?`)){
        updatePersonData(person.id)
        console.log(nameObject.name)
    }
  }

    return(
        <form onSubmit = {addName}>
        <div>
          name: <input 
          value = {newName}
          onChange = {handleNameChange}/>
        </div>
        <div>
          number: <input 
          value = {newNum}
          onChange = {handleNumChange}/>
        </div>
        <div>
          <button type="submit">add</button>
        </div>
      </form>
    )
}
export default PersonForm